import { Component, OnInit } from '@angular/core';
import { JobService } from '../service/job.service';
import * as _ from 'lodash';

@Component({
  selector: 'app-jobs-list',
  templateUrl: './jobs-list.component.html',
  styleUrls: ['./jobs-list.component.scss']
})
export class JobsListComponent implements OnInit {
  allJobs: any[] = [];
  jobs: any[] = [];
  pagination: any = {
    currentPage: 1,
    totalPages: 1,
    pageSize: 10,
    totalRecords: 25,
    itemsText: ''
  };

  constructor(private _jobService: JobService) { }

  ngOnInit() {
    this.getJobs();

  }

  getJobs(): void {
    this._jobService.getJobs().subscribe((res: any) => {
      let jobsTemp: any[] = [];
      console.log(res);
      // this.allJobs = res.fiopsJobs;
      res.fiopsJobs.forEach(item => {
        let status, statusClass, description, feeder, grid;
        let customDesc = item.description.split('@');
        description = customDesc[0];
        if (customDesc.length > 1) {
          let feederGrid = customDesc[1].split(':');
          if (feederGrid[0] === 'Feeder') {
            feeder = feederGrid[1];
            grid = '';
          }
          else {
            grid = feederGrid[1];
            feeder = '';

          }
        }
        if (item.subJobs.filter(x => x.subjob_status === 'PULLED').length > 0) {
          status = 'IN-PROGRESS';
          statusClass = 'label label-info';
        }
        else if (item.subJobs.filter(x => x.subjob_status === 'QUEUED').length > 0) {
          status = 'QUEUED';
          statusClass = 'label label-info';
        }
        else if (item.subJobs.filter(x => x.subjob_status === 'SUCCESS').length === item.subJobs.length) {
          status = 'SUCCESS';
          statusClass = 'label label-success';
        }
        else {
          status = item.subJobs.filter(x => x.subjob_status !== 'SUCCESS' && x.subjob_status !== 'QUEUED' && x.subjob_status !== 'PULLED')[0].subjob_status;
          statusClass = 'label label-danger';
        }
        jobsTemp.push({
          jobId: item.fiopsJobID,
          description: description,
          startTime: new Date(item.submissionTime),
          userName: item.username,
          status: status,
          class: statusClass,
          feeder: feeder,
          grid: grid
        });
      });
      this.allJobs = _.sortBy(jobsTemp, 'jobId').reverse();
      this.jobs = this.allJobs;
      //this.applyPagination();
    });

  }

  applyPagination(buttonType?: string): void {
    if (!buttonType) {
      this.pagination.currentPage = 1;
    }
    else {
      switch (buttonType) {
        case 'NEXT_PAGE':
          this.pagination.currentPage += 1;
          break;
        case 'PREV_PAGE':
          this.pagination.currentPage -= 1;
          break;
        case 'FIRST_PAGE':
          this.pagination.currentPage = 1;
          break;
        case 'LAST_PAGE':
          this.pagination.currentPage = this.pagination.totalPages;
          break;
      }
    }
    this.jobs = this.allJobs.slice((this.pagination.currentPage - 1) * 10, this.pagination.currentPage * 10);
    this.pagination.totalPages = Math.ceil(this.allJobs.length / 10);
    this.pagination.totalRecords = this.allJobs.length;
    this.pagination.itemsText = 'Displaying ' + (((this.pagination.currentPage - 1) * 10) + 1) + ' - ' + ((this.pagination.currentPage * 10) > this.allJobs.length ? this.allJobs.length : (this.pagination.currentPage * 10)) + ' of ' + this.allJobs.length;
  }

}



