import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { JobExecutionComponent } from './job-execution/job-execution.component';
import { JobDetailsComponent } from './job-details/job-details.component';
import { JobsListComponent } from './jobs-list/jobs-list.component';
import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { JobService } from './service/job.service';
import { Ng2AutoCompleteModule } from 'ng2-auto-complete';
import { HttpClientModule } from '@angular/common/http';
import { Ng2SmartTableModule } from 'ng2-smart-table';
import { ModalModule } from 'ngx-bootstrap/modal';
import { ToastrModule } from 'ngx-toastr';
import { EsriMapModule } from '../esriMap/esri-map.module';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    Ng2SmartTableModule,
    Ng2AutoCompleteModule,
    EsriMapModule,
    ModalModule.forRoot(),
    HttpClientModule,
    ToastrModule.forRoot(),
    RouterModule.forChild([
      { path: 'job-execution', component: JobExecutionComponent },
      { path: 'job-details/:jobId', component: JobDetailsComponent },
      { path: 'job-list', component: JobsListComponent }
    ]),
  ],
  providers: [JobService],
  declarations: [JobExecutionComponent, JobDetailsComponent, JobsListComponent]
})
export class JobsModule { }



