import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Http } from '@angular/http';

const CATEGORIES: any[] = [
  { name: "ADMS Validation", value: "ADMS" }
];

@Injectable()
export class JobService {

  private apiUrl: string = 'https://dte-api.cyient-fiops.com/api/fiops/';
  private accessToken = localStorage.getItem('access-token') || '';
  private FME_TOKEN: any = '9df1738776a52a97d3888a7c39ccd1abd5f46921';

  constructor(private _http: HttpClient) { }

  submitFmeJob(fmeUrl: string, publishParameter: any) {
    var url = fmeUrl;
    let header = new HttpHeaders();
    let tokentoSend = 'fmetoken token=' + this.FME_TOKEN;
    header = header.append('Authorization', tokentoSend);
    header = header.append('Content-Type', 'application/json');
    header = header.append('Accept', 'application/json');
    var body = {
      "publishedParameters": publishParameter,
      "subsection": "REST_SERVICE"

    };
    console.log(body);
    return this._http.post(url, JSON.stringify(body), { headers: header }) as Observable<any>;
  }

  postFiopsJob(description: string, fme_job_ids: any) {
    const url = this.apiUrl + 'jobs';
    let body = {
      "description": description,
      "fme_job_ids": JSON.stringify(fme_job_ids),
      "username": "RaviTeja.Viswanadha"
    };
    return this._http.post(url, body, { headers: new HttpHeaders({ 'cyient-access-token': this.accessToken }) }) as Observable<any>;
  }

  getCategories(): Observable<any> {
    let url: string = this.apiUrl + 'rulesengine/ruleset';
    return this._http.get(url, { headers: new HttpHeaders({ 'cyient-access-token': this.accessToken }) }) as Observable<any>;
  }

  getRuleSetByCategory(category: number): Observable<any> {
    let url: string = this.apiUrl + 'rulesengine/ruleset/' + category;
    return this._http.get(url, { headers: new HttpHeaders({ 'cyient-access-token': this.accessToken }) }) as Observable<any>;
  }

  getFeedersAndGrids(): Observable<any> {
    return this._http.get('./assets/data/data.json');
  }

  getJobs(): Observable<any> {
    let url: string = this.apiUrl + 'jobs';
    return this._http.get(url, { headers: new HttpHeaders({ 'cyient-access-token': this.accessToken }) }) as Observable<any>;
  }

  getJobDetails(id: number): Observable<any> {
    let url = this.apiUrl + 'jobs/' + id;
    return this._http.get(url, { headers: new HttpHeaders({ 'cyient-access-token': this.accessToken }) });
  }

  getJobKPIData(id: number): Observable<any> {
    let url = this.apiUrl + 'reports';
    return this._http.get(url, { headers: new HttpHeaders({ 'cyient-access-token': this.accessToken }) }).map(res => this.filterReportByJob(id, res));
  }

  filterReportByJob(jobId: number, res: any): any {
    let filteredDataByJob = res.filter(x => +x.fiops_job_id === jobId);
    let result = {
      errors_written: 0,
      features_read: 0
    };
    filteredDataByJob.forEach(element => {
      result.features_read += +element.features_read;
      result.errors_written += +element.errors_written || 0;
    });
    return result;
  }

  getReportDetails(jobId: number): Observable<any> {
    let url = this.apiUrl + 'reports/details/' + jobId;
    return this._http.get(url, { headers: new HttpHeaders({ 'cyient-access-token': this.accessToken }) });
  }



}



