import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { JobService } from '../service/job.service';
import { EsriMapComponent } from '../../esriMap/esriMap/esri-map.component';

@Component({
  selector: 'app-job-details',
  templateUrl: './job-details.component.html',
  styleUrls: ['./job-details.component.scss']
})
export class JobDetailsComponent implements OnInit {
  @ViewChild('myMap') map: EsriMapComponent;
  selectedJobId: number;
  kpiData: any = {
    numOfValidatedFeatures: 0,
    numOfErrors: 0,
    numOfRules: 0
  };

  errorData: any[] = [];
  mapView: any;

  constructor(private _router: ActivatedRoute, private _jobService: JobService) {

  }

  ngOnInit() {
    this.selectedJobId = +this._router.snapshot.paramMap.get('jobId');
    this.getJobDetails();
    this.getKPIData();
  }

  getJobDetails(): void {
    this._jobService.getJobDetails(this.selectedJobId).subscribe((res: any) => {
      this.kpiData.numOfRules = res.fiopsJob.subJobs.length;
    });
  }

  getKPIData(): void {
    this._jobService.getJobKPIData(this.selectedJobId).subscribe(res => {
      this.kpiData.numOfErrors = res.errors_written || 0;
      this.kpiData.numOfValidatedFeatures = res.features_read || 0;
      if (this.kpiData.numOfErrors > 0) {
        this.getErrorData();
      }
    });

  }

  onMapLoad(mapView: any) {
    this.mapView = mapView;    
  }

  getErrorData(): void {
    this._jobService.getReportDetails(this.selectedJobId).subscribe((res: any) => {
      this.errorData = res.features;
    });

  }

  locateFeature(feature: any) {

    let lat, long;

    if (Array.isArray(feature.geometry.coordinates[0])) {
      long = feature.geometry.coordinates[0][0];
      lat = feature.geometry.coordinates[0][1];
    }
    else {
      long = feature.geometry.coordinates[0];
      lat = feature.geometry.coordinates[1];
    }

    let graphic = {
      geometry: {
        type: "point", // autocasts as new Point()
        longitude: long,//.longitude,
        latitude: lat
      },
      symbol: {
        type: "picture-marker",
        url: "assets/img/pin.jpeg",
        height: "30px",
        width: "30px"
      }

    };
    this.map.addGraphic(graphic);

  }

}



