import { Component, OnInit, ViewChild } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ToastrService } from 'ngx-toastr';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { JobService } from '../service/job.service';
import { Job } from '../models/jobs-model';
import * as _ from 'lodash';

@Component({
  selector: 'app-job-execution',
  templateUrl: './job-execution.component.html',
  styleUrls: ['./job-execution.component.scss']
})
export class JobExecutionComponent implements OnInit {
  @ViewChild('childModal') childModal: ModalDirective;

  categories: any[] = [];
  ruleSetData: any[] = [];
  ruleBaseData: any[] = [];
  ruleTypes: string[] = [];
  ruleNames: string[] = [];
  feeders: string[] = [];
  grids: string[] = [];
  selectedCategory: any;
  selectedRuleType: string;
  selectedRuleName: string;
  disableGridDropdown: boolean = false;
  disableFeederDropdown: boolean = false;
  isJobsSelected: boolean = false;
  input: string = '<input type="checkbox">';
  isAllRowsSelected: boolean = false;
  settings: any = {
    columns: {
      checkbox: {
        title: 'Check Box',
        type: 'html',
        valuePrepareFunction: (value) => { return this._sanitizer.bypassSecurityTrustHtml(this.input); },
        filter: false
      },

      businessRuleType: {
        title: 'Rule Type'
      },
      businessRuleName: {
        title: 'Rule Name'
      }
    },
    attr: {
      class: "smart-size"
    },
    pager: {
      display: true,
      perPage: 5
    },
    actions: {
      add: false,
      edit: false,
      delete: false
    }
  };

  jobsModal = new Job('', 'Select Feeder', 'Select Grid');

  constructor(private _jobService: JobService, private _sanitizer: DomSanitizer, private toastr: ToastrService) { }

  ngOnInit() {
    this.getCategories();
    this.getFeedersAndGrids();
  }

  getCategories(): void {
    this._jobService.getCategories().subscribe((res: any) => {
      this.categories = res.rulesets;
      this.selectedCategory = this.categories[0].id;
      this.getRules();
    });
  }

  onCategoryChange(e) {
    this.selectedRuleType = '';
    this.selectedRuleName = '';
    this.getRules();
  }

  onRuleTypeChange(e) {
    console.log(e);
    this.selectedRuleType = e;
    let rules = [];
    this.ruleSetData.forEach(x => {
      if (x.businessRuleType === this.selectedRuleType && x.businessRuleName) {
        rules.push(x.businessRuleName);
      }
    });
    this.ruleNames = _.uniq(rules);
    this.filterRuleBaseData();

  }

  onRowSelect(e, item) {
    if (item) {
      if (e.target.checked) {
        this.isAllRowsSelected = this.ruleBaseData.filter(x => x.selected).length === this.ruleBaseData.length;
      }
      else {
        this.isAllRowsSelected = false;
      }

    }
    else {
      this.ruleBaseData.forEach(item => item.selected = e.target.checked);
    }
    this.isJobsSelected = this.ruleBaseData.filter(x => x.selected).length > 0;
  }

  onRuleTypeModelChange(e) {
    // console.log(e);
    if (!e) {
      this.selectedRuleType = '';
      console.log(this.selectedRuleType);
      this.filterRuleBaseData();
    }

  }

  onRuleNameChange(e) {
    this.selectedRuleName = e;
    this.filterRuleBaseData();
  }

  onRuleNameModelChange(e) {
    if (!e) {
      this.selectedRuleName = '';
      this.filterRuleBaseData();
    }
  }

  getRules(): void {
    this._jobService.getRuleSetByCategory(this.selectedCategory).subscribe((res: any) => {
      console.log(res);
      this.ruleSetData = res.ruleBase;
      this.ruleBaseData = res.ruleBase;
      this.isJobsSelected = false;
      this.isAllRowsSelected = false;
      this.fillRules();
    });

  }

  fillRules() {
    this.ruleTypes = _.uniq(_.map(this.ruleSetData, x => x.businessRuleType));
    this.ruleNames = _.uniq(_.map(this.ruleSetData, x => x.businessRuleName));
  }

  filterRuleBaseData() {
    let filteredData = [];
    if (this.selectedRuleType || this.selectedRuleName) {
      if (this.selectedRuleType) {
        filteredData = this.ruleSetData.filter(x => x.businessRuleType === this.selectedRuleType);
      }
      if (this.selectedRuleName) {
        if (filteredData.length > 0) {
          filteredData = filteredData.filter(x => x.businessRuleName === this.selectedRuleName);
        }
        else {
          filteredData = this.ruleSetData.filter(x => x.businessRuleName === this.selectedRuleName);
        }
      }
      this.ruleBaseData = filteredData;
    }
    else {
      this.ruleBaseData = this.ruleSetData;
    }

  }

  showChildModal(): void {
    this.childModal.show();
  }

  hideChildModal(): void {
    this.jobsModal = new Job('', 'Select Feeder', 'Select Grid');
    this.childModal.hide();
  }

  getFeedersAndGrids() {
    this._jobService.getFeedersAndGrids().subscribe((res: any) => {
      this.feeders = res.results.feeders;
      this.grids = res.results.grids;
    });
  }

  onSelectionChange(type: string): void {
    if (type === 'Feeder') {
      if (this.jobsModal.feeder === 'Select Feeder') {
        this.disableGridDropdown = false;
      }
      else {
        this.disableGridDropdown = true;
        this.jobsModal.grid === 'Select Grid';
      }

    } else {
      if (this.jobsModal.grid === 'Select Grid') {
        this.disableFeederDropdown = false;
      }
      else {
        this.disableFeederDropdown = true;
        this.jobsModal.feeder === 'Select Feeder';
      }

    }
  }

  executeJob(): void {
    let selectedRules = this.ruleBaseData.filter(x => x.selected);
    let jobCount = 0;
    let femJobIds = [];
    selectedRules.forEach(item => {
      let publishParams: any[] = JSON.parse(item.publishedParameters);
      publishParams = publishParams.filter(x => x.name.toUpperCase() === 'FEEDER_JOBUNIT' || x.name.toUpperCase() === 'VALUE');
      if (this.jobsModal.feeder !== 'Select Feeder') {
        publishParams[0].value = "@Value(FEEDER_NAME)"
        publishParams[1].value = this.jobsModal.feeder;
      }
      else {
        publishParams[0].value = "@Value(JOBUNIT)"
        publishParams[1].value = this.jobsModal.grid;
      }
      // console.log(JSON.stringify(publishParams));
      this._jobService.submitFmeJob(item.url, publishParams).subscribe(res => {
        if (res) {
          if (res.id) {
            femJobIds.push({ 'jobid': Number(res.id) });
            jobCount++;
          }
          if (jobCount === selectedRules.length) {
            this.postFiopsJob(femJobIds);
          }
        }
      });

    });
  }

  postFiopsJob(jobIds) {
    let postData = { 'jobs': jobIds };
    let customDesc = this.jobsModal.description + '@' + (this.jobsModal.feeder !== 'Select Feeder' ? 'Feeder:' + this.jobsModal.feeder : 'Grid:' + this.jobsModal.grid);
    this._jobService.postFiopsJob(customDesc, postData).subscribe((res: any) => {
      if (res.status.toUpperCase() === 'SUCCESS') {
        console.log("Job created successfully: " + res.fiopsJobID);
        this.hideChildModal();
        this.toastr.success("Job created successfully: " + res.fiopsJobID);
      }
    });
  }

}



